$arrowBgDefault: white;
// $arrowBgHover: #4c4c4c;
$arrowShadow: 0px 6px 15px rgba(0, 0, 0, 0.05);
$arrowSize: 36px;
$arrowIcon: url('../../assets/images/common/slider_arrow.svg');

.react-slick-carousel {
  width: 100%;
  .green-dots {
    .slick-dots {
      button {
        &:before {
          font-size: 36px;
          color: #00D7AF;
        }
      }
    }
  }

  .vertical-dots {
    .slick-dots {
      transform: rotate(90deg);
      left: -60%;
      bottom: 50%;
    }
  }

  .white-sliders,
  .white-sliders-padded {
    .slick-arrow {
      box-shadow: $arrowShadow;
      background-color: #5F8CD1;
      height: $arrowSize;
      width: $arrowSize;
      transform: unset;
      border-radius: 50%;
      color: transparent;
      z-index: 100;
      transition: background-color 0.3s ease-in-out;
      top: 0;
      bottom: 0;
      margin-top: auto;
      margin-bottom: auto;
      transition: transform 0.3s ease;
      

      &::before {
        display: block;
        color: transparent;
        background-color: transparent;
        background-image: $arrowIcon;
        background-repeat: no-repeat;
        background-position: center center;
        filter: saturate(0);
      }

      &:hover {
        transform: scale(1.1);
        // background-color: $arrowBgHover;
      }
      &:active {
        transition: transform 0.1s;
        transform: scale(1);
        // background-color: $arrowBgHover;
      }

      &.slick-prev::before {
        transform: rotate(180deg);
      }
    }

    .slick-prev { left: -24px; }
    .slick-next { right: -24px; }
    @media screen and (max-width: 991px) {
      .slick-prev { left: 0; }
      .slick-next { right: -48px; }
    }
  }

  .white-sliders {
    @media screen and (max-width: 991px) {
      max-width: calc(100% - 48px);
      padding-left: 48px;
      .slick-prev { left: 0; }
      .slick-next { right: -48px; }
    }
  }

  .white-sliders-padded {
    max-width: calc(100% - 48px);
    padding-left: 48px;
    .slick-prev { left: 0; }
    .slick-next { right: -48px; }
  }
}