.productionProcess {
  padding: 0 30px;
  margin-bottom: 100px;

  @media screen and (max-width: 991px) {
    padding: 0 40px;
  }

  [class="react-slick-carousel"] {
    [class*="slick-track"] {
      display: flex !important;
    }
  
    [class*="slick-slide"] {
      height: inherit !important;
  
      > div {
        height: 100%;
      }
  
      [tabIndex="-1"] {
        height: 100%;
      }
    }
    
    @media screen and (max-width: 991px) {
      [class*="slick-slider"] {
        padding: 0 !important;
        max-width: 100%;

        [class*="slick-prev"] {
          transform: translateX(-38px);
        }
        [class*="slick-nex"] {
          transform: translateX(-10px);
        }
      }
      
      // [class*="slick-arrow"] {
      //   display: none !important;
      // }
      li {
        button {
          &:before {
            font-size: 2rem;
          }
        }
      }
    }
  }
}

.videoContainer {
  padding: 15px;
  @media screen and (max-width: 991px) {
    padding: 0;
  }

  .videoInner {
    height: 100%;
    background-color: white;
    // max-width: 600px;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    padding: 30px;
    display: flex;
    justify-content: center;
    align-items: center;

    @media screen and (max-width: 991px) {
      padding: 0;
      box-shadow: none;
    }

    iframe, img {
      border-radius: 20px;
      border: none;
      width: 100%;
    }

    button {
      height: 100%;
      position: relative;
      padding: 0;
      transition: all 0.3s ease;
      &:hover {
        transform: scale(1.05);
      }
      &:active {
        transition: all 0.1s;
        transform: scale(1);
      }
      img {
        height: 100%;
        object-fit: cover;
      }
    }

    i {
      position: absolute;
      top: calc(50% - 25px);
      left: calc(50% - 25px);
      color: black;
      opacity: 0.7;
      font-size: 50px;
    }
  }
}